#DashboardScreen {

	.card-events {
		border-radius: 0px !important;
	}

	ons-splitter {
		color: #fff;
		font-weight: bold;

		li.list-group-item {
			border: none;
			background: #fff;
			color: #fff;
			padding: .75rem 1.25rem;

			a.list-group-item, a:not([href]):not([tabindex]) {
				border: none !important;
				background: transparent;
				color:  #00b894;
				padding: 0px !important;
			}
		}

		li.list-group-item.first-item {
			background: #00b894;
			font-weight: bold;
			div.profile-picture {
				background: url('../img/profile-image.png');
				width: 4rem;
				height: 4rem;
				margin-bottom: 10px;
				border-radius: 50%;
				border: 1px solid #fff;
			}
		
		}

		li.list-group-item.clickable {
			border-top: 0.5px solid #efefef;;
		}

		li.list-group-item.clickable-last {
			border-top: 0.5px solid #efefef;;
			border-bottom: 0.5px solid #efefef;;
		}

		div.page__background {
			background: #fff;
		}
	}
	
	.list-icon {
		font-size: 1.5rem;
		padding-left: 0.5rem;
		padding-top: 0.2rem;

	}

	.navbar-header{
		color: #000000;
		font-weight: bold;
		margin-left: 30px;
	}

	.search-field {
		border-radius: 30px;
    	background-color: #f7f4f4;
	}

	.title {
		font-weight: bolder;
		font-size: larger;
		font-family: auto;
		color: #00b894;
	}
	
	.notification {
		font-size:22px; 
		color: #ad0317;
	}

	//dashboard content
	.row-contents{
		display: flex;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		margin-right: -15px;
		margin-left: -15px;
		margin-bottom: 0.5rem !important;
	}
	.button-see-all{
		text-align: end !important;
		color: #00b894;
	}
	.card-events{
		padding: 0px !important;
		margin: 0px !important;
	}
	.card-body-events{
		padding: 0px !important;
		padding-left: 5px !important;
	}
	.card-title{
		padding-top: 5px !important;
		margin: 0px !important;
		font-weight: bold;
		font-size: 12px !important;	
	}
	.card-contents{
		font-size: 10px;
	}

	//People Met
	.row-contents{
		margin-left: -2rem;
		margin-right: -2rem;
	}
	.card-people{
		padding: 0px !important;
		height: 4.5rem !important;
	}
	.card-people-image{
		padding: 3px !important;
	}
	.card-body-people{
		padding: 0px !important;
		padding-left: 0px !important;
		margin-left: -1rem ;
	}

	.card-image {
		width: 100%; 
		height: 15vw;
		object-fit: cover;
	}
}


